<template>
  <div>
    <h2 align="center">{{ label }}</h2>
    <canvas :id="this.label"></canvas>
  </div>
</template>

<script>
import Chart from "chart.js";
import axios from "axios";

export default {
  props: ["endpoint", "whatToGet", "label"],
  name: "PlanetChart",
  data() {
    return {
      data: [],
      date: [],
    };
  },
  mounted() {
    //"http://localhost:8090/" +
    axios.get(this.endpoint).then((response) => {
      //console.log(response.data);
      this.date = response.data.map((d) => d.Time.substring(10));
      this.data = response.data.map((d) => d[this.whatToGet]);
      const ctx = document.getElementById(this.label);
      new Chart(ctx, generateData(this.data, this.date, this.label));
    });
  },
};

function generateData(data, date, label) {
  //console.log(data);
  return {
    type: "line",
    title: "ad",
    data: {
      labels: date,
      datasets: [
        {
          label: label,
          data: data,
          borderColor: "#36495d",
          borderWidth: 1,
        },
      ],
    },
    options: {
      responsive: true,
      lineTension: 1,
      scales: {
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
              padding: 25,
            },
          },
        ],
      },
    },
  };
}
</script>